.wrapper {
    margin: 0 auto
}

.events-table {
    margin-top: 5%;
    margin-bottom: 5%;
}

.events-table div {
    padding: 0.2em;
}

.events-table .table-header {
    position: sticky;
    top: 0;
    background-color: #96a8d7;
    border-radius: 0.3em;
    font-size: 2em;
    font-weight: bold;
}

.events-table .table-header .event-name {
    padding: 0.5em;
}

.events-table .table-header .counter {
    border: 0;
    background-color: #4a65cc;
    color: #d7e3f4;
    border-radius: 0.5em;
}

.events-table .line .counters {
    display: flex;
}

.events-table .line {
    margin: 0;
    padding: 0;
}

.events-table .line .level-1 h6 {
    font-weight: 650;
}

.events-table .line .level-2 {
    padding-left: 2em;
}

.events-table .line .level-3 {
    padding-left: 3em;
}

.events-table .line .level-4 {
    padding-left: 4em;
}

.events-table .line .counters .counter {
    min-width: 8%;
    margin: 0 auto;
    text-align: center;
    border-left: 1px solid #96a8d7;
}

